@font-face {
  font-family: "TT Lakes";
  src: url(TTLakes-Regular.fa93e70b0194a593ac03cc30392c8008.woff2) format "woff2";
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "TT Lakes";
  src: url(TTLakes-Medium.e781c4423f37bcb7f986369a3eec667f.woff2) format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "TT Lakes";
  src: url(TTLakes-Bold.cdc7dcddf6ee8d85fcc20a6fb1b0669d.woff2) format "woff2";
  font-weight: 700;
  font-style: normal;
}
html {
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  color: #1b1c21;
  height: 100%;
}
html *,
html *::before,
html *::after {
  box-sizing: inherit;
}

body {
  font-family: "TT Lakes", "Arial", "sans-serif";
  min-height: 100vh;
  margin: 0;
  padding: 0;
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #f8f8f8;
  color: #1b1c21;
  position: relative;
}

a {
  text-decoration: none;
  color: #1b1c21;
}
a:hover {
  color: #ff3e79;
}

.title {
  color: #1b1c21;
  font-family: "TT Lakes", "Arial", "sans-serif";
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: -0.6px;
  min-height: 40px;
  align-items: flex-start;
}

.center__wrap {
  display: block;
  max-width: 1120px;
  overflow: hidden;
  z-index: 1000;
}

.link {
  color: inherit;
  text-decoration: none;
  display: block;
  font-style: normal;
  cursor: pointer;
  transition: all 0.3s ease-in;
}
.link:hover {
  color: #ff3e79;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.94);
  z-index: 2900;
  transition: opacity 0.3s ease;
  opacity: 0;
}
.overlay--active {
  display: block;
  opacity: 1;
}

.container {
  min-height: auto;
  max-width: 1440px;
  padding: 0 16px;
  margin: 0 auto;
}

.rounded-icon {
  width: 40px;
  height: 40px;
  background-color: #ff3e79;
  border-radius: 50%;
  border: none;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
  transition: all 0.3s ease-in;
}
.rounded-icon:hover {
  background-color: #ec3e72;
  border-color: #ec3e72;
  box-shadow: 0 5px 9px rgba(0, 0, 0, 0.1);
}
