  background-image: url(data:image/svg+xml;base64,);
}
@media (min-width: 1440px) {
  .rounded-icon--close-btn body {
    min-width: 1440px;
  }
  .rounded-icon--close-btn {
    display: none;
    visibility: hidden;
    pointer-events: none;
  }
}
.rounded-icon--search-btn {
  background-image: url(data:image/svg+xml;base64,);
}
