  background-size: 10px;
}
.rounded-icon--brands:hover {
  filter: drop-shadow(0 6px 12px rgba(241, 48, 154, 0.2));
  transform: scale(1.1);
  background-color: #fff;
  background-size: 11px;
}

.header-page {
  max-width: 1440px;
}
.header-page__list {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 24px 24px 46px 24px;
  justify-content: space-between;
}
.header-page__wrapper {
  display: flex;
  align-items: center;
  justify-items: flex-start;
  gap: 20px;
}

.header {
  background-color: #fff;
  border-bottom: 1px solid #b8ffec;
  position: relative;
  grid-area: header;
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
  padding: 18px 0;
}
.header__item {
  display: flex;
  align-items: center;
}
.header__item--logo {
  flex-grow: 2;
  gap: 15px;
}
.header__item--logo::after {
  content: "";
  width: 2px;
  height: 32px;
  border-radius: 1px;
  background-color: #d9fff5;
}
@media (min-width: 768px) {
  .header__item--logo::before {
    content: "";
    width: 2px;
    height: 32px;
    border-radius: 1px;
    background-color: #d9fff5;
  }
  .header__item--logo::after {
    display: none;
  }
}
.header__item--tablet, .header__item--mobile {
  padding: 0;
  margin: 0;
}
.header__item--tablet {
  display: none;
}
@media (min-width: 768px) {
  .header__item--tablet {
    display: flex;
    align-items: center;
    gap: 15px;
    transform: translateY(3px);
  }
  .header__item--tablet::after {
    content: "";
    width: 2px;
    height: 32px;
    border-radius: 1px;
    background-color: #d9fff5;
  }
}
@media (min-width: 1120px) {
  .header__item--tablet {
    transform: translateY(5px);
  }
}
.header__item--mobile {
  display: flex;
  align-items: center;
  gap: 15px;
}
.header__link {
  display: inline-block;
}
@media (min-width: 1120px) {
  .header {
    display: block;
  }
  .header__item--logo {
    flex-grow: 2;
  }
}
@media (min-width: 1440px) {
  .header {
    display: none;
  }
}

.aside-page {
  display: none;
  flex-direction: column;
  width: 320px;
  min-height: 100vh;
  font-family: inherit;
  font-weight: 500;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3000;
  visibility: hidden;
  background-color: #fff;
  transition: all 0.3s ease-in;
  animation: asideOut 0.4s ease-in-out forwards;
}
.aside-page.aside-page--active {
  display: flex;
  visibility: visible;
  height: 100%;
  animation: asideIn 0.4s ease-in-out forwards;
}
@media (min-width: 1440px) {
  .aside-page body {
    min-width: 1440px;
  }
  .aside-page {
    display: flex;
    flex: 0 0 320px;
    visibility: visible;
    box-shadow: none;
    flex-direction: column;
    position: static;
    transition: none;
    min-height: 100% !important;
    animation: asideIn 0.4s ease-in-out forwards;
    grid-area: aside;
    z-index: 2800;
  }
}
@keyframes asideIn {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes asideOut {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(-100%);
    opacity: 0;
  }
}
.aside-page .header-page {
  max-width: 1440px;
}
.aside-page .header-page__list {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 24px 24px 46px 24px;
  justify-content: space-between;
}
.aside-page .header-page__wrapper {
  display: flex;
  align-items: center;
  justify-items: flex-start;
  gap: 20px;
}
.aside-page .header-page__wrapper .header-page__link img {
  max-width: 100%;
}
.aside-page .main-aside .main-nav__list {
  display: flex;
  flex-direction: column;
}
.aside-page .main-aside .main-nav__list__item {
  margin-bottom: 10px;
}
.aside-page .main-aside .main-nav__list__item .main-nav__link {
  text-decoration: none;
}
.aside-page .main-aside .main-nav__list__item .main-nav__link--is-active {
  font-weight: bold;
}
.aside-page .footer .contact-container {
  display: flex;
  justify-content: space-between;
}
.aside-page .footer .contact-container .contact-menu {
  display: flex;
}
.aside-page .footer .contact-container .contact-menu li {
  margin-right: 10px;
}
.aside-page .footer .contact-container .address__mail-link, .aside-page .footer .contact-container .address__tel-link {
  display: block;
  margin-bottom: 5px;
}
.aside-page .footer .lang {
  display: flex;
  font-weight: 500;
  text-transform: uppercase;
  list-style: none;
  padding: 0 0 32px 0;
}
.aside-page .footer .lang__item {
  margin-right: 10px;
}
.aside-page .footer .lang__item__link--active {
  font-weight: bold;
}

@keyframes modalIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes modalOut {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}
input[type=tel],
input[type=email],
input[type=text],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.modal-feedback {
  display: none;
  position: fixed;
  width: 320px;
  height: 100%;
  font-family: inherit;
  font-weight: 500;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);
  top: 0;
  right: 0;
  z-index: 3000;
  visibility: hidden;
  background-color: #fff;
  animation: modalOut 0.4s ease-in-out forwards;
}
.modal-feedback--active {
  display: flex;
  flex-direction: column;
  visibility: visible;
  transition-delay: 0s;
  animation: modalIn 0.4s ease-in-out forwards;
}
.modal-feedback__title {
  position: relative;
  margin-top: 40px;
  margin-left: -16px;
  padding-left: 16px;
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: -0.6px;
  display: flex;
  gap: 12px;
}
.modal-feedback__title::before {
  content: "";
  display: block;
  height: 40px;
  width: 4px;
  background-image: url("data:image/svg+xml,%3Csvg width='4' height='40' viewBox='0 0 4 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4 4C4 1.79086 2.20914 0 0 0V40C2.20914 40 4 38.2091 4 36V4Z' fill='%2341F6D7'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: left center;
}
.modal-feedback__input, .modal-feedback__textarea {
  width: 100%;
  height: 48px;
  padding: 10px 16px;
  margin-bottom: 16px;
  color: #b5b6bc;
  font-family: "TT Lakes", "Arial", "sans-serif";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  border: 1px solid #d9fff5;
  border-radius: 8px;
}
.modal-feedback__input:focus, .modal-feedback__textarea:focus {
  outline: 2px solid #ff3e79;
  box-shadow: 0 0 5px rgba(255, 62, 121, 0.5);
}
.modal-feedback__textarea {
  height: 118px;
}
.modal-feedback__info {
  position: relative;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  margin-bottom: 16px;
  padding: 0 16px;
  color: #7e7e82;
}
.modal-feedback__link {
  display: inline-block;
  color: #ff3e79;
  transition: all 0.3s ease-in;
}
.modal-feedback__link:hover {
  text-decoration: underline;
}
.modal-feedback__wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}
.modal-feedback__button {
  position: absolute;
  z-index: 10;
  align-self: flex-end !important;
  bottom: -27px !important;
