    grid-area: footer;
    max-width: 1440px !important;
  }
}
@media (min-width: 768px) {
  .footer-main {
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    padding: 32px;
    margin: 0 10px;
  }
}
.footer-main__wrapper {
  margin-bottom: 16px;
}
@media (min-width: 1120px) {
  .footer-main__wrapper {
    display: flex;
    flex-basis: 33% !important;
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .footer-main__wrapper {
    display: flex;
    flex-basis: 33% !important;
    flex-direction: column;
  }
  .footer-main__wrapper:first-child {
    text-align: left;
  }
  .footer-main__wrapper:nth-child(2) {
    text-align: center;
  }
}
.footer-main__link {
  color: #7e7e82;
}
.footer-main__link:hover {
  color: #1b1c21;
  text-decoration: underline;
  -webkit-text-decoration-color: #1b1c21;
          text-decoration-color: #1b1c21;
}
@media (min-width: 768px) {
  .footer-main__text:last-child {
    text-align: right;
  }
}